  // 景区舆情监测
  initalECharts4() {
    let that = this
    let {
      data
    } = this.state
    this.setState({
      myChart4: echarts.init(document.getElementById('scenic'), 'dark')
    },
      () => {
        this.state.myChart4.setOption({
          backgroundColor: 'transparent',
          tooltip: {
            trigger: 'item'
          },
          series: [{
            name: '访问来源',
            type: 'pie',
            radius: ['65%', '80%'],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 10,
              borderColor: 'transparent',
              borderWidth: 2,
              color: function (params) {
                //自定义颜色
                var colorList = ['#454AFA', '#0CCF73', '#EEA754']
                return colorList[params.dataIndex]
              }
            },
            label: {
              show: false,
              position: 'center',
              normal: {
                show: true,
                position: 'center',
                formatter: function (param, pre, cut) {
                  if (param.dataIndex === that.state.index) {
                    return `${param.name}\n  {per|${param.percent}%} `
                  }
                  return ''
                },
                rich: {
                  // abg: {
                  //     backgroundColor: '#333',
                  //     width: '100%',
                  //     align: 'right',
                  //     height: 22,
                  //     borderRadius: [4, 4, 0, 0]
                  // },

                  b: {
                    fontSize: 16,
                    lineHeight: 33
                  },
                  per: {
                    color: '#eee',
                    backgroundColor: '#334455',
                    padding: [2, 4],
                    borderRadius: 2
                  }
                }
              }
            },
            emphasis: {
              label: {
                show: true,
                fontWeight: 'bold'
              }
            },
            labelLine: {
              show: false
            },
            data: data
          }]
        })
        this.state.myChart4.dispatchAction({
          type: 'highlight',
          seriesIndex: 0,
          dataIndex: this.state.index
        })

        this.getWord('canvas', data[this.state.index])

        this.state.myChart4.on('mouseover', function (e) {
          let index = e.dataIndex
          if (index !== that.state.index) {
            this.dispatchAction({
              type: 'downplay',
              seriesIndex: 0,
              dataIndex: that.state.index
            })
          }
        })
        this.state.myChart4.on('mouseout', function (e) {
          this.dispatchAction({
            type: 'highlight',
            seriesIndex: 0,
            dataIndex: that.state.index
          })
        })
        // 监听饼图点击事件
        // 当鼠标移入时，如果不是第一项，则把当前项置为选中，如果是第一项，则设置第一项为当前项
        this.state.myChart4.on('click', function (e) {
          let index = e.dataIndex
          this.dispatchAction({
            type: 'downplay',
            seriesIndex: 0,
            dataIndex: that.state.index
          })
          this.dispatchAction({
            type: 'highlight',
            seriesIndex: 0,
            dataIndex: index
          })
          that.setState({
            index: index
          },
            () => {
              this.setOption(this.getOption())
              that.getWord('canvas', data[that.state.index])
            }
          )
        })
      }
    )
  }
