  //渲染词云
  getWord(canvas, data) {
    var options = eval({
      list: data.list, //或者[['各位观众',45],['词云', 21],['来啦!!!',13]],只要格式满足这样都可以
      gridSize: 12, // 密集程度 数字越小越密集
      weightFactor: 1, // 字体大小=原始大小*weightFactor
      maxFontSize: 60, //最大字号
      minFontSize: 14, //最小字号
      fontWeight: 'normal', //字体粗细
      fontFamily: 'STKaiti', // 字体
      color: 'random-light', // 字体颜色 'random-dark' 或者 'random-light'
      backgroundColor: 'transparent', // 背景颜色
      rotateRatio: 0 // 字体倾斜(旋转)概率，1代表总是倾斜(旋转)
    })
    //生成
    WordCloud(canvas, options)
  }
