export default function getDate() {
  var now = new Date()
  var weeks = new Array(
    '星期日',
    '星期一',
    '星期二',
    '星期三',
    '星期四',
    '星期五',
    '星期六'
  )
  var day = now.getDay()
  var week = weeks[day]
  var year = now.getFullYear()
  var month = now.getMonth() + 1
  month = month < 10 ? '0' + month : month
  var date = now.getDate()
  date = date < 10 ? '0' + date : date
  var HH = now.getHours()
  var MM = now.getMinutes()
  var SS = now.getSeconds()
  HH = HH < 10 ? '0' + HH : HH
  MM = MM < 10 ? '0' + MM : MM
  SS = SS < 10 ? '0' + SS : SS
  return (
    year +
    '-' +
    month +
    '-' +
    date +
    ' ' +
    week +
    ' ' +
    HH +
    ':' +
    MM +
    ':' +
    SS
  )
}
